.img{
  width: 100%;
}

.content{
  padding: 20px;
  display: flex;
}

.preloader{
  text-align: center;
  margin-top: 30%;
}

.profilePhoto{
  border-radius: 50%;
}

.descriptionBlock{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 50px;
}

.descriptionBlock div {
  margin-bottom: 20px;
}

.blue{
  color: rgb(61, 105, 202);
}

.bold{
  font-weight: 700;
}

.red{
  color: rgb(255, 8, 0)
}

.upperCase{
  text-transform: uppercase;
}


.myContactsLinks{
  display: flex;
  flex-wrap: wrap;
}

.myContactsLinks a{
  margin-right: 20px;
  transition: all .3s ease
}

.myContactsLinks a:hover{
  transform: scale(1.1);
}

.myContactsImg{
  cursor: pointer;
  width: 50px;
  min-height: 50px;
  border-radius: 50%;
}