.title{
  color: red;
}

.buttonMore{
  margin-top: 20px;
  width: 100px;
  height: 40px;
  align-self: center;
}

.wrapper{
 display: flex;
 flex-direction: column;
 align-items: center;

}

.content{
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.buttonFolow{
  margin-top: 20px;
}

.card{
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.userInfo{
  border-radius: 20px;
  border: 2px solid green;
  padding: 5px;
}

.avatar{
  max-width: 75px;
  border-radius: 50%;
  transition: all .3s ease;
  cursor: pointer;
}

.avatar:hover{
  transform: scale(1.1)
}

.avatarBlockWrap{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.userInfo{
  min-width: 600px;
  margin-left: 40px;
  display: flex;
  justify-content: space-between;
}

.pagesButtons{
  display: flex;
  margin-top: 10px;
}

.pageBut{
  margin-right: 10px;
  cursor: pointer;
  border: 2px solid red;
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: all .3s ease;
}

.pageBut:hover{
  background-color: rgb(104, 223, 50);
}

.selected{
  border:2px solid green;
  font-weight: 700;
}

.pageBut:last-child(){
  margin-right: 0;
}

.wrapperContent{
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.preloader{
  text-align: center;
  margin-top: 30%;
}

