.nav{
  grid-area: n;
  background-color: rgb(84, 52, 228);
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 827px ;
}

/* link */

.linkWrap{
  margin-top: 20px;
}

.link{
  color: rgb(206, 206, 50);
  text-decoration: none;
  transition: all .3s ease;
  margin-top: 20px;
}

.link:hover{
  color: white;
}

.link:active{
  color: greenyellow;
}

.active{
  color: white;
  text-decoration: none;
  transition: all .3s ease;
}

